<template>
  <button @click="sendCode" v-html="text"
          :class="{'disabled':!enable||sending}"
          class="seed-code border">
  </button>
</template>

<script>
export default {
  name: 'SeedCode',
  props: {
    enable: {
      type: Boolean,
      default: false
    },
    params: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  data() {
    return {
      duration: 60,
      sending: false,
      isFirstSend: true
    }
  },
  computed: {
    text() {
      if (this.sending) {
        return `(<span class="monospaced-font">${this.duration}</span>)`
      } else if (!this.isFirstSend) {
        return ''
      } else {
        return ''
      }
    }
  },
  methods: {
    sendCode() {
      if (!this.enable || this.sending) return
      this.$emit('sendCodeBefore')
      this.$api.sendCode(this.params).then(({ head, body }) => {
        if (head.errCode === '0000') {
          this.timeout()
          this.$emit('sendCallBack', body.messageId)
          this.$toast.success(`${body.expire / 60}`)
        } else {
          this.$emit('sendCallBack', null)
        }
        this.isFirstSend = false
      }).catch(() => {
        this.$emit('sendCallBack', null)
      })
    },
    timeout() {
      this.sending = true
      const timer = setInterval(() => {
        this.duration--
        if (this.duration <= 0) {
          this.duration = 60
          this.sending = false
          clearInterval(timer)
        }
      }, 1000)
    }
  }
}
</script>

<style lang="scss">
  .seed-code {
    width: rem(100);
    height: rem(28);
    font-size: rem(14);
    line-height: rem(28);
    color: $--color-primary;
    text-align: center;

    &.disabled {
      color: #c0c4cc;
      border-color: #ccc;

      &::after {
        border-color: #ccc;
      }
    }
  }
</style>
