<template>
  <div class="sign">
    <div class="sign__header clearfix">
      <i @click="$router.replace('/')" class="sign__back"></i>
      <button @click="$router.replace('/login')" class="sign__button"></button>
    </div>
    <h1 class="sign__title">
      {{isFast?'':''}}
    </h1>
    <form @submit.prevent class="form">
      <template v-if="isFast">
        <div class="form__item">
          <label class="form__label" for="sign_in_code">
            
          </label>
          <div class="form__content form__content--suffix">
            <input v-model.trim="fastForm.captcha"
                   @input="replaceNonNum"
                   class="form__input sign-up-form__input " id="sign_in_code"
                   maxlength="4" type="text">
            <img @click="getCaptcha" class="form__captcha" :src="image" alt="">
          </div>
        </div>
      </template>
      <template v-else>
        <div class="form__item">
          <label class="form__label" for="sign_up_acc">
            
          </label>
          <div class="form__content">
            <input v-model.trim="phoneNumber"
                   @input="replaceNonNum"
                   class="form__input sign-up-form__input" id="sign_up_acc"
                   maxlength="12" placeholder="" type="text">
            <SeedCode @sendCodeBefore="sendCodeBefore"
                      @sendCallBack="sendCallBack"
                      :params="sendCodeParams"
                      :enable="enableSendBtn">
            </SeedCode>
          </div>
        </div>
        <div class="form__item">
          <label class="form__label" for="sign_up_pwd">
            6
            <a class="form__link-service" href="javascript:void (0);"></a>
          </label>
          <div class="form__content">
            <input v-model.trim="phoneForm.smsCode" class="form__input sign-up-form__input" id="sign_up_pwd"
                   @input="replaceNonNum"
                   maxlength="6" placeholder="6" type="text">
          </div>
        </div>
        <div class="form__item">
          <label class="form__label" for="sign_up_parent">
            
          </label>
          <div class="form__content">
            <input v-model.trim="phoneForm.parentId" class="form__input sign-up-form__input" id="sign_up_parent"
                   @input="replaceNonNum"
                   maxlength="10" placeholder="10" type="text">
          </div>
        </div>
      </template>
      <button @click="submit" :class="{'disabled-opacity':!validateForm}" class="form__button form__button--full">
        
      </button>
      <button @click="isFast=!isFast" class="form__button form__button--empty border">
        {{isFast?'':''}}
      </button>
    </form>
    <p class="sign__notes sign-up__notes">
      USDT <span class="sign__notes-strong">  </span> 
    </p>
  </div>
</template>

<script>
import cryptoJS from 'crypto-js'
import { mapActions, mapMutations } from 'vuex'
import SeedCode from '@/components/SeedCode.vue'
import { validatePhoneNum } from '@/utils/validate.js'

export default {
  name: 'sign_up',
  components: { SeedCode },
  data() {
    return {
      image: null,
      isFast: false,
      phoneNumber: null,
      fastForm: {
        captcha: null,
        captchaId: null,
        randomStr: Date.now()
      },
      phoneForm: {
        parentId: null,
        smsCode: null,
        messageId: null
      },
      sendCodeParams: {
        use: 1,
        mobileNo: null
      }
    }
  },
  computed: {
    validateForm() {
      if (this.isFast) {
        return this.fastForm.captcha && this.fastForm.captcha.length === 4
      } else {
        return this.phoneNumber && this.phoneNumber.length >= 11 &&
          this.phoneForm.smsCode && this.phoneForm.smsCode.length === 6
      }
    },
    enableSendBtn() {
      return validatePhoneNum(this.phoneNumber)
    }
  },
  created() {
    this.getCaptcha()
    this.toggleSideBar(false)
  },
  methods: {
    ...mapMutations([
      'toggleSideBar',
      'setCustomerInfo'
    ]),
    ...mapActions([
      'getCustomerInfo',
      'getBalanceInfo'
    ]),
    replaceNonNum(e) {
      e.target.value = e.target.value.replace(/[^0-9]/ig, '')
    },
    getCaptcha() {
      const params = {
        _isNeedLoading: false
      }
      this.$api.getCaptcha(params).then(({ head, body }) => {
        if (head.errCode === '0000') {
          this.fastForm.captchaId = body.captchaId
          this.image = 'data:image/jpeg;base64,' + body.image
        }
      })
    },
    submit() {
      let result
      if (!this.validateForm) return
      if (this.isFast) {
        this.fastForm.randomStr = Date.now()
        result = this.$api.createAccountAuto(this.fastForm)
      } else {
        result = this.$api.createAccountByMobileNo(this.phoneForm)
      }
      result.then(({ head, body }) => {
        if (head.errCode === '0000') {
          const params = {
            _isNeedLoading: false
          }
          this.setCustomerInfo(body)
          this.getCustomerInfo(params)
          this.getBalanceInfo(params)
          this.$toast.success({
            message: '',
            callback: () => {
              const loginName = body.loginName
              let password = body.password
              if (!this.isFast) {
                password = cryptoJS.enc.Utf8.parse(body.customerId)
                password = cryptoJS.DES.decrypt(body.x2, password, {
                  mode: cryptoJS.mode.ECB,
                  padding: cryptoJS.pad.Pkcs7
                })
                password = cryptoJS.enc.Utf8.stringify(password)
              }
              this.$router.replace({
                name: 'registerSuccess',
                params: {
                  loginName: loginName,
                  password: password,
                  isFast: this.isFast
                }
              })
            }
          })
        }
      }).catch(e => {
        console.log(e)
      })
    },
    sendCodeBefore() {
      this.sendCodeParams.mobileNo = this.$encrypted(this.phoneNumber)
    },
    sendCallBack(messageId) {
      this.phoneForm.messageId = messageId
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "sign.scss";

  .sign-up {
    &-form {
      &__input {
        font-size: rem(16);
      }
    }

    &__notes {
      margin-top: rem(25);
    }
  }
</style>
